.content_container {
  height: calc(100vh - 200px);
  overflow: auto;
}

.wordle {
  margin: 0 auto;
}

.title {
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 4px;
  text-align: center;
  margin: 0 auto 40px;
  width: 500px;
}

.toolbar {
  width: 500px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.num_guesses_setting {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.num_characters_setting {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  justify-content: space-between;
}

.spacer {
  flex-grow: 1;
}

.guesses {
  width: 500px;
  padding-bottom: 40px;
  margin: 0 auto;
}

.guess_row,
.keyboard_row {
  display: flex;
  justify-content: center;
}

.guess_character_div {
  width: 62px;
  height: 62px;
  margin: 4px;
}

.guess_character_empty {
  border: 2px solid #d3d6da;
}

.guess_character {
  font-size: 32px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.guess_character_unknown,
.guess_character_not_used {
  border: 2px solid #787c7e;
}

.guess_character_wrong_position {
  border: 2px solid #c9b458;
}

.guess_character_correct {
  border: 2px solid #6aaa64;
}

.keyboard {
  width: 500px;
  margin: 0 auto;
  position: fixed;
  left: calc(50% - 250px);
  bottom: 0;
  background: white;
  height: 200px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.keyboard_character {
  width: 40px;
  height: 58px;
  border-radius: 4px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Clear Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  margin: 4px;
}

.status_unknown {
  background: #d3d6da;
}

.status_not_used {
  background: #787c7e;
  color: white;
}

.status_wrong_position {
  background: #c9b458;
  color: white;
}

.status_correct {
  background: #6aaa64;
  color: white;
}

.notification_message {
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: bold;
  background: black;
  color: white;
  border-radius: 8px;
  align-items: center;
  padding: 16px;
  margin-top: 100px;
}
